<template>
	<div style="width: 100%">
    <v-skeleton-loader
      v-if="loading && items.length === 0"
      type="title"
    />
    <flicking 
      v-else
      :options="{ circular: true, horizontal: false }" 
      :plugins="plugins" 
      class="px-2 py-4"
    >
      <div
        v-for="(item, index) in items"
        :key="index"
        @click="$store.dispatch('open', { link: item.link })"
      >
        <v-chip small color="black" text-color="white" class="mx-2">공지사항</v-chip>
        <span class="text-subtitle-2">{{ item.title }}</span>
      </div>
    </flicking>
  </div>
</template>

<script>
import { AutoPlay } from '@egjs/flicking-plugins';
export default {
  name: 'VerticalSlide',
  props: [
    'items',
    'loading',
  ],
  data: () => ({
    plugins: [new AutoPlay({ duration: 3000 })],
  }),
};
</script>