import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/search',
    name: 'search',
    component: () => import(/* webpackChunkName: "about" */ '../views/SearchView.vue'),
  },
  {
    path: '/contents',
    name: 'contents',
    component: () => import(/* webpackChunkName: "about" */ '../views/ContentsView.vue'),
  },
  {
    path: '/my',
    name: 'my',
    component: () => import(/* webpackChunkName: "about" */ '../views/MyView.vue'),
  },
  {
    path: '/page/:id',
    name: 'page',
    component: () => import(/* webpackChunkName: "about" */ '../views/PageView.vue'),
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return { x: 0, y : 0 };
  },
});

export default router;
