<template>
	<div style="width: 100%">
    <v-skeleton-loader
      v-if="loading && items.length === 0"
      type="image"
    	:width="width"
      class="ma-auto"
  	/>
    <flicking
      v-else
      :options="{
        circular: true
      }"
      :plugins="plugins"
    >
      <div 
        v-for="(item, index) in items"
        :key="index"
        class="pa-2"
      >
        <v-card 
          flat 
          :color="item.background" 
          :width="width"
          :height="height"
          link
          @click="$store.dispatch('open', { link: item.link })"
        >
          <v-container class="fill-height">
  					<v-row class="fill-height align-center">
              <v-col cols="6" class="pa-6">
                <div class="font-weight-bold">
                  {{ item.title }}
                </div>
  						</v-col>
              <v-col cols="6">
                <v-img 
                  :src="item.image"
                  width="80"
                  height="80"
                  contain
                />
  						</v-col>
  					</v-row>
  				</v-container>
        </v-card>
      </div>
    </flicking>
  </div>
</template>

<script>
import { AutoPlay } from '@egjs/flicking-plugins';
export default {
  name: 'HorizontalBanner',
  props: [
    'items',
    'width',
    'height',
    'loading',
  ],
  data: () => ({
    plugins: [new AutoPlay({ duration: 5000 })],
  }),
};
</script>