  <div>
    <v-app-bar
      flat 
      color="white"
      class="align-end"
    >
      <div class="d-flex align-end">
        <img
          class="shrink mr-1"
          src="/img/logo.png"
          width="24px"
        />
        <div 
          class="font-weight-bold blue--text text--darken-4"
          style="font-size: 18px;"
        >
          부천YWCA
        </div>
        <div 
          class="ml-2 text-caption"
        >
          Young Women's Christian Association
        </div>
      </div>
      <v-spacer/>
    </v-app-bar>
    
    <section>
      <Carousel
        :items="featured"
        :loading="loading"
        aspect-ratio="2"
      />
    </section>
    
    <section class="background-white"> 
  		<v-container>
        <v-row class="pa-1">
          <v-col 
            v-for="(item, index) in menus"
            :key="index"
            cols="3"
            class="pa-4 pb-0"
            @click="$store.dispatch('open', { link: item.link })"
          >
            <div class="icon-area text-center pa-2">
              <img :src="item.icon" width="34"/>
            </div>
            <div class="text-caption text-center mt-1">
              {{ item.title }}
					  </div>
  				</v-col>
  			</v-row>
  		</v-container>
  	</section>
        
    <section 
      v-for="(item, index) in items"
      :key="index"
      class="pt-6"
    >
  		<v-container>
  			<v-row>
  				<v-col cols="12" class="text-h6 font-weight-bold">
            {{ item[0].category }}
  				</v-col>
          <component
            v-bind:is="item[0].style"
            :items="item"
            :loading="loading"
            :width="item[0].width"
            :height="item[0].height"
          />
  			</v-row>
  		</v-container>
  	</section> 
    
    <section class="mt-8 background-white">
      <div style="width: 100%; border-bottom: 1px solid lightgrey"/>
      <Vertical-Slide
        :items="notices"
        :loading="loading"
      />
  	</section>
    
    <Footer/>
  </div>
</template>
<script>
import Carousel from '../components/Carousel';
import HorizontalSlide from '../components/HorizontalSlide';
import HorizontalCard from '../components/HorizontalCard';
import HorizontalBanner from '../components/HorizontalBanner';
import VerticalSlide from '../components/VerticalSlide';
import Footer from '../components/Footer';
import jsonp from 'jsonp';

export default {
  name: 'HomeView',
  components: {
    Carousel,
    HorizontalSlide,
    HorizontalCard,
    HorizontalBanner,
    VerticalSlide,
    Footer,
  },
  data: () => ({
    loading: false,
    menus: [
      {
        icon: '/img/icons8-megaphone-100.png',
        title: '공지사항',
        link: '/contents#notices',
      },
      {
        icon: '/img/icons8-news-100.png',
        title: '소식',
        link: '/contents#news',
      },
      {
        icon: '/img/icons8-storytelling-100.png',
        title: '도서관',
        link: '/contents#books',
      },
      {
        icon: '/img/icons8-photo-gallery-100.png',
        title: '갤러리',
        link: '/contents#photos',
      },
      {
        icon: '/img/icons8-light-on-100.png',
        title: '지음',
        link: 'https://www.jium.club',
      },
      {
        icon: '/img/icons8-peace-pigeon-100.png',
        title: '피스오브피스',
        link: 'https://www.piecepeace.org'
      },
      {
        icon: '/img/icons8-broom-100.png',
        title: '가사돌봄',
        link: '/page/16BxuLcYSYM5-eRUF-9VztjO0JisgKjAsBDHUszz4cuc',
      },
      {
        icon: '/img/icons8-paint-palette-100.png',
        title: '미술관소개',
        link: '/page/1TzRjOQkgTwyHUhk6IcUFcKlrGny93v5wUv9jQKlh2WY',
      },
      {
        icon: '/img/icons8-replace-100.png',
        title: 'RE100',
        link: '/page/1DHtsGg5C3yTngW8ftX8c8qiH4b__v_i1Sa5xCR1DYfg',
      },
      {
        icon: '/img/icons8-power-plant-100.png',
        title: '탈핵생명',
        link: '/page/1HeedBgFJMlTp3rAxXRPcrVQ69qN0ubPT7TL8kJG4WU4',
      },
      {
        icon: '/img/icons8-ukraine-100.png',
        title: '우크라이나',
        link: '/page/1iFEt0NV9UwXfRXdhzUcOWv6eebUkw-JbdDmboIeRv5w',
      },
      {
        icon: '/img/icons8-growing-plant-100.png',
        title: '평화와통일',
        link: '/page/1-xA8HDqhUOnBhUFsZVOqTfKMpGSlvHvcl6KaMJT8u9k',
      },
    ],
    featured: [],
    notices: [],
    items: [],
  }),
  async created () {
    this.loading = true;
  	const data = await this.$store.dispatch('getData', { type: 'home' });
    let temp = '';
  	data.forEach(d => {
      switch (d.category) {
        case 'featured': this.featured.push(d); break;
        case 'notices': this.notices.push(d); break;
        default: 
          if (d.category !== temp) {
            temp = d.category;
            this.items.push([]);
          }
          this.items[this.items.length - 1].push(d);
          break;
      }
    });
    this.loading = false;
  },
};
</script>

<style scoped>
  .icon-area {
   	border-radius: 20%;
    background-color: #f9f9f9;
