import Vue from 'vue';
import Vuex from 'vuex';
import jsonp from 'jsonp';
import router from '../router'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
  },
  getters: {
  },
  mutations: {
  },
  actions: {
    search({}, payload) {
      return new Promise((resolve, reject) => {
        jsonp(`${process.env.VUE_APP_API}?type=${payload.type}&search=${payload.search}`, { name: payload.type }, function (err, data) {
          if (err) reject(err);
          resolve(data);
        });
      });
    },
    getData({}, payload) {
      return new Promise((resolve, reject) => {
        jsonp(`${process.env.VUE_APP_API}?type=${payload.type}&chunk=${payload.chunk}`, { name: payload.type }, function (err, data) {
          if (err) reject(err);
          resolve(data);
        });
      });
    },
    open({}, payload) {
      if (payload.link.startsWith('/')) {
	      router.push(payload.link);
      } else {
    		window.open(payload.link, '_blank');
  		}
    },
  },
  modules: {
  },
});
