<template>
	<div style="width: 100%">
    <v-skeleton-loader
      v-if="loading && items.length === 0"
      type="image"
    />
    <flicking
      v-else
      :options="{ circular: true }"
      :plugins="plugins" 
    >
      <v-card
        v-for="(item, index) in items"
        :key="index"
        flat
        tile
        width="100%"
        link
        @click="$store.dispatch('open', { link: item.link })"
      >
        <v-img
          :src="item.image"
          class="flicking-image"
          gradient="to bottom, rgba(0,0,0,0), rgba(0,0,0,.3)"
        >
          <v-card 
            flat 
            dark
            color="transparent" 
            class="fill-height d-flex align-end"
          >
            <div class="pa-6"> 
              <div class="pb-2">
                <v-chip color="white" text-color="black">{{ item.tag }}</v-chip>
              </div>
              <div class="text-h6 font-weight-bold">
                {{ item.title }}
              </div>
              <div class="">
                {{ item.text }}
              </div>
            </div>
          </v-card>
        </v-img>
      </v-card>
    </flicking>
  </div>
</template>

<script>
import { AutoPlay } from '@egjs/flicking-plugins';

export default {
  name: 'Carousel',
  props: [
    'items',
    'loading',
  ],
  data: () => ({
    plugins: [new AutoPlay({ duration: 10000 })],
  }),
};
</script>

<style scoped>
  .flicking-image { 
   	width: 100%;
    object-fit: cover;
    overflow: hidden;
    aspect-ratio: 1.5;
  }
</style>